(function(module) {
try {
  module = angular.module('diroop.tools.templateCache');
} catch (e) {
  module = angular.module('diroop.tools.templateCache', []);
}
module.run(['$templateCache', function($templateCache) {
  $templateCache.put('drTemplateCache:/version/version.html',
    '<div class="dr-version"><small>{{$ctrl.version}}</small></div>');
}]);
})();
